* {
  box-sizing: border-box;
}

.page {
  font-family: Roboto, sans-serif;
}

.page__body {
  margin: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  padding: 16px;
  display: flex;
}

.card__image {
  object-fit: cover;
  width: 160px;
  height: 134px;
  margin-top: 16px;
  display: block;
}

.card__info {
  margin-top: 40px;
}

.card__name {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-items: end;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-tag {
  justify-content: space-between;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.card__price-value, .card-value {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-link {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy-link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars :last-child {
  margin-right: 0;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
